import React, { useState } from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';
import { Dropdown, Button, Modal } from 'react-bootstrap';
import validator from 'validator';
import '../App.css';


function ForgotPasswordWindow(props) {
  return (
    <Modal
      {...props}
      size=""
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Rate My City
        </Modal.Title>
      </Modal.Header>

      <Modal.Body>
        <p>
          We will send you an email based on the address you've written with a link that will allow you 
          to reset your password. Please follow the instructions on the email.
        </p>
      </Modal.Body>

      <Modal.Footer>
        <Button onClick={props.onHide}>Close</Button>
      </Modal.Footer>

    </Modal>
  );
}

function Login() {
  const [modalShow, setModalShow] = React.useState(false);
  const history = useHistory();
  const [show, toggle] = React.useState(false);
  const ToggleShow = () => toggle(true);
  const ToggleHide = () => toggle(false);

  var firstName = '';
  var lastName = '';  
  var emailAddress = '';
  var userName = '';
  var loginPassword = '';
  var confirmPassword = '';

  function handleClickCancel() {
      localStorage.clear();
      history.push("/");
  }

  const [message, setMessage] = useState('');

  const validateEmail = (e) => {
    var email = e.target.value
    if (validator.isEmail(email)) {
      setMessage('')
    } else {
        setMessage('The email is not Valid')
    }
  }

  const validatePassword = (e) => {
    if (validator.isStrongPassword(e, {
      minLength: 8,
      minLowercase: 1,
      minUppercase: 1,
      minNumbers: 1,
      minSymbols: 1
    })) {
        setMessage('')
    } else {
        setMessage('The password in not strong enough')
    }
  }

  const doLogin = async event => {
      event.preventDefault();

      var obj = { email:emailAddress.value, password:loginPassword.value };
      var js = JSON.stringify(obj);

      var config = {
          method: 'post',
          url: 'https://rate-my-city.herokuapp.com/api/login',
          headers: { 'Content-Type': 'application/json' },
          data: js
      };

      axios(config)
          .then(function (response) {
              var res = response.data;
              if (res.error) {
                  setMessage(res.error);
              } else {
                  returnFromLogin(res);
              }
          })

  }

  const doRegister = async event => {
    if (message === '') {

      event.preventDefault();

      var obj = { 
          firstName: firstName.value,
          lastName: lastName.value,
          userName: userName.value,
          email:emailAddress.value, 
          password:loginPassword.value,
          confirmpassword: confirmPassword.value 
      };

      var js = JSON.stringify(obj);

      var config = {
          method: 'post',
          url: 'https://rate-my-city.herokuapp.com/api/register',
          headers: { 'Content-Type': 'application/json' },
          data: js
      };

      axios(config)
          .then(function (response) {
              var res = response.data;
              if (res.error && res.error !== '') {
                  setMessage(res.error);
              } else {
                  returnFromRegister(res);
              }
          })
    }
  }

  function doForgot() {
    setModalShow(true);

      var obj = { 
          email:emailAddress.value, 
      };

      var js = JSON.stringify(obj);

      var config = {
          method: 'post',
          url: 'https://rate-my-city.herokuapp.com/api/resetPassword',
          headers: { 'Content-Type': 'application/json' },
          data: js
      };

      axios(config)
          .then(function (response) {
              var res = response.data;
              if (!res.message) {
                  setMessage(res.message);
              } else {
                  setMessage(res.message);
                  localStorage.setItem("emailToken", res.emailToken);
              }
          })
  }

  function returnFromLogin(result) {
      localStorage.setItem('accessToken', result.accessToken);
      localStorage.setItem('userId', result.id);
      localStorage.setItem('userFirstName', result.firstName);
      localStorage.setItem('userLastName', result.lastName);
      localStorage.setItem('userName', result.userName);
      localStorage.setItem('userEmail', emailAddress.value);
      localStorage.setItem('userPassword', loginPassword.value);
      history.push("/");
  }

  function returnFromRegister(result) {
      localStorage.clear();
      history.push("/");
  }


  return(
    <div className="container-fluid h-custom">
      <div className="row d-flex justify-content-center align-items-center h-100">
        <div className="mt-5 col-xl-4">
          <form >
            <div className="form-group row d-flex justify-content-center">
              <div className="col-md-4">
                <button type="button" className="btn text-white btn-lg fw-bold btn-outline-dark btn-secondary" onClick={ToggleHide}>Login</button>
              </div>
              <div className="col-md-4">
                <button type="button" className="btn text-white btn-lg fw-bold btn-outline-dark btn-secondary" onClick={ToggleShow}>Register</button>
              </div>
            </div>
            
            <div className="divider d-flex align-items-center my-4 mt-0">
              <p className="text-center fw-bold mx-3 mb-4"></p>
            </div>

            {show &&
              <div className="form-outline mt-0 mb-2">
                <input type="text" autoFocus 
                    className="form-control" 
                    placeholder="Enter First Name" 
                    ref={(c) => firstName = c} />
                <label className="form-label">First Name</label>
              </div>
            }
            {show &&
              <div className="form-outline mt-3 mb-2">
                <input type="text" 
                    className="form-control" 
                    placeholder="Enter Last Name" 
                    ref={(c) => lastName = c} />
                <label className="form-label">Last Name</label>
              </div>
            }

            {show &&
              <div className="form-outline mt-3 mb-2">
                <input type="text" 
                      className="form-control" 
                      placeholder="Enter User Name"
                      ref={(c) => userName = c} />
                <label className="form-label">User Name</label>
              </div>
            }

            <div className="form-outline mt-3 mb-2">
              <input type="email" autoFocus 
                  className="form-control form-control-lg" 
                  placeholder="Enter a valid email address" 
                  ref={(c) => emailAddress = c} 
                  onChange={(e) => validateEmail(e)}/>
              <label className="form-label">Email Address</label>
            </div>

            <div className="form-outline my-3 mb-2">
              <input type="password" 
                  className="form-control form-control-lg" 
                  placeholder="Enter password" 
                  ref={(c) => loginPassword = c}
                  onChange={(e) => validatePassword(e.target.value)} />
              <label className="form-label">Password</label>
            </div>
            {show &&
              <div className="form-outline mt-3 mb-2">
                <input type="password" 
                    className="form-control" 
                    placeholder="Confirm password"
                    ref={(c) => confirmPassword = c}
                    onChange={(e) => validatePassword(e.target.value)} />
                <label className="form-label">Confirm password</label>
              </div>
            }

            <div className="form-group row d-flex justify-content-center ">
              {!show && 
                <div className="col-md-4">
                  <button type="button" className="btn text-white btn-lg fw-bold btn-outline-dark btn-secondary" onClick={doLogin}>Sign In</button>
                </div>
              }
              {show && 
                <div className="col-md-4">
                  <button type="button" className="btn text-white btn-lg fw-bold btn-outline-dark btn-secondary" onClick={doRegister}>Sign In</button>
                </div>
              }

              <div className="col-md-4">
                <button type="button" className="btn btn-dark btn-lg fw-bold form-control" onClick={handleClickCancel}>Cancel</button>
              </div>
            </div>

            {!show && 
              <div className="d-flex justify-content-center align-items-center mt-4">
                <a className="link-line" href="#" onClick={() => doForgot()}>Forgot Password ?</a>
              </div>
            }

            <div className="divider d-flex align-items-center my-4">
              <p className="text-center fw-bold mx-3 mb-1">{message}</p>
            </div>

            <ForgotPasswordWindow
                show={modalShow}
                onHide={() => setModalShow(false)}
            />

          </form>
        </div>
      </div>
    </div>

  );
};

export default Login;